.footer {
  background-color: $text-color;
  
  &__content {
    padding: 61px 0 56px;
  
    @include onTablet {
    padding: 93px 0 90px;
    }
  }
  
  &__left-content {
    @include onTablet {
      grid-column: 1 / 5;
    }
  }
  
  &__right-content {
    @include onTablet {
      grid-column: 5 / 9;
    }
  }
  
  &__section {
    margin-bottom: 42px;
  
    @include onTablet {
      margin-bottom: 48px;
    }
  
    @include onDesktop {
      margin-bottom: 88px;
    }
  }
  
  &__logo {
    display: block;
    margin-bottom: 44px;
  
    @include onTablet {
    @media (max-width: 780px) {
        margin-bottom: $marg-bottom-footer-icon-less-780;
    }
  
    @media (min-width: 781px) {
        margin-bottom: $marg-bottom-footer-icon-more-780;
    }
  }
}
  
  &__title-menu {
    margin: 0 0 16px;
  }
  
  &__menu {
    background-color: transparent;
    margin: 0 0 64px;
  
    @include onTablet {
      margin: 0;
    }
  }
  
  &__copyright {
    margin: 0;
    line-height: 150%;
    letter-spacing: -0.01em;
    color: $gray-titles;
  
    @include onTablet {
    grid-column: 5 / 9;
    }
  }
}
